@import "../../../assets/styles/animate.css";

.login__container {
  position: fixed;
  display: flex;
  justify-items: center;
  align-items: center;
  width: 100%;
  height: 100%;

  background-image: linear-gradient(to top, #00c6fb 0%, #005bea 100%);

  .inner {
    min-width: 400px;
    display: table;
    margin: 0 auto;

    .login-form {
      background: snow;
      box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
      padding: 60px 40px 15px 40px;
      width: 100%;

      .ant-form-explain {
        font-size: 10px;
        padding: 5px 0;
      }

      .form-title {
        font-size: 16px;
        text-align: center;
        color: rgba(0, 0, 0, 0.7);
        letter-spacing: 1px;
      }

      .logo {
        padding: 10px 0 5px 0;
        img {
          width: 90px;
          display: table;
          margin: 0 auto;
        }
      }

      .role {
        text-align: center;
        padding-bottom: 35px;
        color: #039be5;
        font-size: 13px;
        font-weight: bold;
        letter-spacing: 1.5px;
      }

      .ant-input {
        min-height: 50px;
      }

      .login-form-button {
        $color: #039be5;
        min-height: 38px;
        margin-top: 10px;
        background: $color;
        border: 0;
        color: white;
        padding: 0 20px;
        padding-top: 3px;
        font-size: 14px;
        transition: 0.2s;

        &:hover {
          background: $color - #111111;
        }
      }

      .form__footer {
        color: gray;
        text-align: center;
        font-size: 9px;
        padding-top: 40px;

        img {
          width: 12px;
          height: 9px;
          margin-right: 3px;
        }
      }
    }
  }
}
